import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import styles from '../styles/HeaderButton.module.css'

function HeaderButton({ buttonName, buttonStyles, header }) {
  function reload() {
    window.location.href = header
  }

  if (header === '#/profile' || header === '#/vetprofile') {
    return <ProfileButton buttonName={buttonName} buttonStyles={buttonStyles} header={header} reload={reload} />
  }
  return (
    <div>
      {buttonStyles === 'enabled' && (
        <a href={header} className={`${styles.Enabled} ${styles.Button}`}>
          <p>{buttonName}</p>
        </a>
      )}
      {buttonStyles === 'disabled' && (
        <a href={header} className={`${styles.Disabled} ${styles.Button}`}>
          <p>{buttonName}</p>
        </a>
      )}
    </div>
  )
}

HeaderButton.propTypes = {
  buttonName: PropTypes.string.isRequired,
  buttonStyles: PropTypes.string.isRequired,
  header: PropTypes.string.isRequired,
}

function ProfileButton({ header, buttonStyles, reload }) {
  const [isVisible, setIsVisible] = useState(false)

  function handleClick() {
    setIsVisible(!isVisible)
  }

  function handleExitClick() {
    handleClick()
    localStorage.clear()
    window.location.href = '#/'
    window.location.reload()
  }

  if (buttonStyles === 'enabled') {
    return (
      <div>
        <div
          role="button"
          onClick={handleClick}
          onKeyDown={handleClick}
          tabIndex="0"
          className={`${styles.Enabled} ${styles.Button}`}
        >
          <p>Профиль</p>
        </div>
        <DropDownList handleClick={handleClick} isVisible={isVisible} header={header} handleExitClick={handleExitClick} reload={reload} />
      </div>
    )
  }
  return (
    <div>
      <div
        role="button"
        onClick={handleClick}
        onKeyDown={handleClick}
        tabIndex="0"
        className={`${styles.Disabled} ${styles.Button}`}
      >
        <p>Профиль</p>
      </div>
      <DropDownList handleClick={handleClick} isVisible={isVisible} header={header} handleExitClick={handleExitClick} reload={reload} />
    </div>
  )
}

ProfileButton.propTypes = {
  header: PropTypes.string.isRequired,
  buttonStyles: PropTypes.string.isRequired,
  reload: PropTypes.func.isRequired,
}

function DropDownList({ isVisible, handleExitClick, header, reload, handleClick }) {
  const dropDown = React.useRef(null)

  function handleClickOutside(event) {
    if (isVisible) {
      if (!dropDown || !dropDown.current.contains(event.target)) {
        handleClick(false)
      }
    }
  }

  useEffect(() => {
    document.addEventListener('click', handleClickOutside, false)
    return function cleanup() {
      document.removeEventListener('click', handleClickOutside, false)
    }
  })

  if (isVisible) {
    return (
      <div ref={dropDown} className={styles.optionsBox}>
        <button type="button" onClick={reload} className={styles.option}>
          <p className={styles.optionText}>Редактировать</p>
        </button>
        <button type="button" onClick={handleExitClick} className={styles.option}>
          Выйти
        </button>
      </div>
    )
  }
  return null
}

DropDownList.propTypes = {
  handleClick: PropTypes.func.isRequired,
  isVisible: PropTypes.bool.isRequired,
  handleExitClick: PropTypes.func.isRequired,
  header: PropTypes.string.isRequired,
  reload: PropTypes.func.isRequired,
}

export default HeaderButton
