import { GET_SEARCH_PETS_REQUEST, GET_SEARCH_PETS_SUCCESS, GET_SEARCH_PETS_FAILURE } from '../constants/ActionTypes'

const initialState = {
  loading: false,
  petsList: [],
  error: null,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case GET_SEARCH_PETS_REQUEST:
      return {
        ...state,
        loading: true,
      }
    case GET_SEARCH_PETS_SUCCESS:
      return {
        loading: false,
        error: null,
        petsList: action.payload,
      }
    case GET_SEARCH_PETS_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
      }
    default:
      return state
  }
}
